.rotate180{
    transform: rotate(180deg);
}

.app-bar{
    background-color: rgb(86, 100, 210) !important;
    color: rgb(255, 255, 255) !important;
    box-shadow: none !important;
    transition: width 195ms cubic-bezier(0.4, 0, 0.6, 1) 0ms,margin 225ms cubic-bezier(0.4, 0, 0.6, 1) 0ms !important;
}

.app-bar-button{
    font-size: 1.75rem !important;
}

.dark-blue{
    color: #3f407d !important;
}

.table-header-cell{
    font-weight: bold !important;
    font-size: 0.9rem !important;
}

.color-error{
    color: red !important;
}